/* TABLE -SIZE */
@mixin tablet {
  @media (min-width: #{$tablet}) {
    @content;
  }
}

/* DESKTOP -SIZE */
@mixin desktop {
  @media (min-width: #{$desktop}) {
    @content;
  }
}

/* GREAT DESKTOP -SIZE */
@mixin lg-desktop {
  @media (min-width: #{$lg-desktop}) {
    @content;
  }
}